import React, {Component} from 'react';
import { connect } from 'react-redux';
import './App.css';
import ButtonMenus from "./components/ButtonMenus/ButtonMenus";
import * as service from './API/API'
import logo from './assets/vic.png'


class App extends Component{

  state = {
    loaded: false
  }

  componentDidMount = () => {
    service.getNames()
      .then(response => {
        const newNames = [...this.props.names];
        const dbNames = response.data;

        for(var n in dbNames){
          const name = {
            sys_id: [n],
            name: dbNames[n][0]
          }

            if(!newNames.includes(name))
            newNames.push(name);
        }

        this.props.updateNames(newNames);


        //Get Random name also
        const length = newNames.length;
        const randomNumber = parseInt(Math.random() * length);
        let defaultName = newNames[randomNumber].name;
        this.props.updateDefaultName(defaultName);

        this.setState({loaded: true})
      });
}

changeName = () => {
  const names = this.props.names
  const length = names.length;
  let randomNumber = parseInt(Math.random() * length);
  let defaultName = names[randomNumber].name;


  //Never get the same default name
  while(defaultName === this.props.defaultName){
      randomNumber = parseInt(Math.random() * length);
      defaultName = names[randomNumber].name;
  }

  this.props.updateDefaultName(defaultName);

}

  render(){
    if(!this.state.loaded){
      return <div/>
    }
    
    return (
      <div className="App">
        <header className="App-header">
          <div className='App-title'>
            <h1>Hello...{this.props.defaultName}</h1>
            <img className="App-logo" src={logo} alt='IamtherealVic'/>
          </div>
          <button className="App-button" onClick={this.changeName}>Change Name</button>
        </header>
        <div className="App-body">
          <div className="App-width">
            <ButtonMenus/>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return{
    defaultName: state.name,
    names: state.names
  };
};

const mapDispatchToProps = dispatch => {
  return{
      updateNames: (value) => dispatch({type: 'UPDATE_NAMES', value: value}),
      updateDefaultName: (value) => dispatch({type: 'UPDATE_DEFAULT_NAME', value: value})
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
